
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  background-color: #f4f4f4;
  color: #333;
}

/* Tela inicial */
.home-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Preenche toda a tela */
  background-color: #fff; /* Cor de fundo branco */
  text-align: center;
  border-radius: 15px; /* Bordas arredondadas */
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.welcome-message h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.welcome-message p {
  font-size: 1.2em;
  margin-bottom: 30px;
}

.enter-button {
  background-color: #20b2aa; /* Cor de fundo do botão verde água */
  color: white;
  padding: 20px 40px;
  border: none;
  border-radius: 10px;
  font-size: 1.5em;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.enter-button:hover {
  transform: scale(1.1); /* Efeito de zoom ao passar o mouse */
}

/* Estilos do Cabeçalho */
.header {
  background-color: #111111;
  color: white;
  padding: 20px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid #f00000; /* Adicionando uma borda ao cabeçalho */
  width: 100%;
}

.header .logo img {
  border-radius: 50%;
  width: 100px; /* Aumento da logo */
  height: 100px;
  transition: transform 0.3s ease-in-out;
}

.header .logo img:hover {
  transform: scale(1.2); /* Efeito de zoom ao passar o mouse */
}

.header h1 {
  flex-grow: 1;
  text-align: center;
}

.header nav ul {
  display: flex;
  justify-content: center;
  padding: 0;
}

.header nav ul li {
  margin: 0 15px;
}

.header nav ul li a {
  color: white;
  font-size: 1.2em;
}

.header nav ul li a:hover {
  color: #00f0dc;
  font-size: 1.3em;
  transition: all 0.3s ease;
}

/* seção sobre */

section #sobre {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Estilos do Conteúdo Principal */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  border-radius: 20px; /* Bordas arredondadas */
  padding: 20px;
  background-color: #ffffff;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

main {
  width: 100%;
  padding: 100px;
  text-align: center;
}

section {
  margin-bottom: 40px;
}

/* Seção de Sobre Nós */
.sobre-nos-container,
.servicos-container,
.desenvolvimento-web-container,
.galeria-container {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 15px; /* Bordas arredondadas */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
  text-align: left; /* Alinhando o texto à esquerda */
}

.sobre-nos-container p,
.servicos-container p,
.desenvolvimento-web-container p,
.galeria-container p {
  font-size: 1.1em;
  line-height: 1.6;
}

/* Estilos do Formulário de Contato */
.contact-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 15px; /* Bordas arredondadas */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.contact-form .form-field {
  margin-bottom: 15px;
}

.contact-form .form-field label {
  display: block;
  margin-bottom: 5px;
}

.contact-form .form-field input,
.contact-form .form-field textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.contact-form .form-field textarea {
  height: 100px;
}

.contact-form button {
  width: 100%;
  padding: 10px;
  background-color: #00c0f0;
  border: none;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s;
}

.contact-form button:hover {
  transform: scale(1.1); /* Efeito de zoom no botão */
}

/* Estilos da Galeria */
.gallery {
  text-align: center;
  margin: 20px 0;
  
}

.image-container {
  width: 80%;
  max-width: 400px;
  margin: 0 auto;
}

.gallery img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.gallery img:hover {
  transform: scale(1.05); /* Efeito de zoom na imagem */
}

.navigation-buttons {
  margin-top: 20px;
}

.navigation-buttons button {
  background-color: #00c0f0;
  padding: 10px 20px;
  border: none;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s;
  margin: 0 10px;
}

.navigation-buttons button:hover {
  transform: scale(1.1); /* Efeito de zoom nos botões */
}

.navigation-buttons .prev-button {
  margin-right: 10px;
}

/* Estilos para os Botões de Navegação e Formulário */
button {
  background-color: #333;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

button:hover {
  transform: scale(1.1); /* Efeito de zoom no botão */
}

/* Estilos do Rodapé */
footer {
  width: 100%;
  background-color: #0a0a0a;
  color: white;
  padding: 20px;
  text-align: center;
  margin-top: 20px;
  border-top: 3px solid #f00000; /* Adicionando uma borda no rodapé */
}

/* Responsividade */
@media (max-width: 768px) {
  .header h1 {
    font-size: 2em;
  }

  nav ul {
    flex-direction: column;
  }

  nav ul li {
    margin: 10px 0;
  }

  .service {
    margin: 10px 0;
  }

  .contact-form input,
  .contact-form button {
    width: 90%;
  }

  .gallery img {
    max-width: 80%;
  }

  .contact-form {
    padding: 20px;
  }

  .contact-form h2 {
    font-size: 1.8em;
  }

  /* Ajustando as containers das seções */
  .sobre-nos-container,
  .servicos-container,
  .desenvolvimento-web-container,
  .galeria-container {
    width: 90%;
    max-width: 900px;
    margin: 20px auto;
    padding: 15px;
  }
}